.register__button {
  width: 358px;
  height: 50px;
  background-color: #000000;
  border-style: none;
  cursor: pointer;
  margin: 186px 0 15px 0;
  border-radius: 2px;
  background-color: #ffffff;
}
.register__button:hover {
  opacity: 0.8;
}
@media screen and (max-width: 768px) {
  .register__button {
    margin: 28px 0 0 0;
  }
}
.register__button:disabled {
  background-color: #e5e5e5;
}
