.header__title{
    font-family:'Inter';
    font-style:normal;
    font-weight:400;
    font-size:18px;
    line-height:22px;
    text-align:right;
    color:#A9A9A9;
    border-style: none;
    cursor: pointer;
    text-decoration: none;
}
.header__title:hover{
    opacity: 0.5; 
}
@media screen and (max-width: 500px){
    .header__title{
        margin-bottom: 40px;
        text-align:center;
    }
}